<template>
  <div class="father">
    <div @click="quanxuan">
      <p>全选</p>
      <div class="tutu">
        <img
          :class="{ reds: zhuangtai }"
          src="../../../assets/img/xiangqing/duigou.png"
        />
      </div>
    </div>
    <div>合计：￥{{ jiagezong }}</div>
    <div>去支付</div>
  </div>
</template>
<script>
export default {
  name: "Gouzong",
  methods: {
    quanxuan() {
      this.$store.commit("quanbu");
    },
  },
  computed: {
    zhuangtai() {
      if (this.$store.state.arr.length !== 0) {
        let flog = 0;
        for (let i = 0; i < this.$store.state.arr.length; i++) {
          if (this.$store.state.arr[i].isshow == false) {
            flog = 1;
            this.$store.state.zhuantai = false;
            break;
            }
        }
        if (flog == 0) {
          this.$store.state.zhuantai = true;
        }
        return this.$store.state.zhuantai;
      }
    },
    jiagezong() {
      let aa = 0;
      for (let i = 0; i < this.$store.state.arr.length; i++) {
        if (this.$store.state.arr[i].isshow == true) {
          aa =aa +this.$store.state.arr[i].count * this.$store.state.arr[i].price;
        }
      }
      return aa;
    },
  },
};
</script>
<style scoped>
.father {
  line-height: 45px;
  width: 100%;
  height: 45px;
  display: flex;
  position: fixed;
  bottom: 50px;
  background-color: rgb(230, 230, 230);
}
.father div:nth-child(1) {
  width: 20%;
  display: flex;
  color: black;
  font-size: 17px;
}
.father div:nth-child(2) {
  flex: 1;
  color: red;
  font-size: 17px;
}
p {
  flex: 1;
}
.tutu {
  flex: 1;
  position: relative;
}

img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid black;
}
.reds {
  background-color: red;
}
.father div:nth-child(3) {
  width: 30%;
  background-color: red;
  color: white;
  text-align: center;
}
</style>