<template>
  <div class="home animate__animated animate__fadeIn">
    <naver class="navers">
      <div slot="center">购物车({{$store.state.arr.length}})</div>
    </naver>
    <scroll class="content" ref="scroll">
    <gouwuinfo></gouwuinfo>
    </scroll>
    <gouzong></gouzong>
    <div class="wu" v-show="ww">亲！您没添加任何商品哦！
      <p class="pp" @click="shou">去看看</p>
      </div>
  </div>
</template>
<script>
import Scroll from "../../components/content/Scroll.vue";
import Naver from "../../components/common/naver/Naver.vue";
import Gouwuinfo from "./Gouwuchezi/Gouwuinfo.vue";
import Gouzong from "./Gouwuchezi/Gouzong.vue";
export default {
  name: "Gouwuche",
  components: {
    Naver,
    Gouwuinfo,
    Scroll,
    Gouzong,
  },
  data() {
    return {};
  },
  computed: {
    ww() {
      if (this.$store.state.arr.length == 0) {
        return true;
      }
    },
  },
  methods: {
    shou() {
      this.$router.push("/shouyess");
    },
  },
  activated() {
    //这一步不能用watch监听vuex中arr的长度，因为当此页面处于deacivated时，scroll中的高度和宽度为0
    this.$refs.scroll.refresh();
  },
};
</script>
<style scoped>
.navers {
  color: white;
  background-color: red;
  font-weight: 700;
  font-size: 19px;
}
.home {
  height: 100vh;
  position: relative;
}
.wu {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  white-space: nowrap;
  font-size: 17px;
  text-align: center;
}
.content {
  height: calc(100% - 141px);
  width: 100%;
  overflow: hidden;
  position: fixed;
  top: 44px;
}
.pp {
  font-size: 16px;
  color: white;
  width: 30%;
  margin: 10px auto;
  background-color: red;
  padding: 5px 0px;
  border-radius: 4px;
}
</style>